.App {
  /* text-align: center; */
  display: flex;
  align-items: stretch;
  min-height: 100vh;
  background-color: var(--color-background);
  background-image: url("background-images/swirl.png");
  /* filter: opacity(0.5) grayscale(50%); */
  background-blend-mode: screen;
  backdrop-filter: blur(100px);

}

.main-page {
  /* position: relative;
  left: var(--header-thickness); */
  margin: 
    var(--header-thickness) 
    1em
    0em
    calc(var(--header-thickness)*1.2);
  padding: 2%;
  padding-top: 0;
  flex: 1;
  overflow-y: auto;
  scroll-behavior: smooth;
  height: 100vh;
  height: 88vh;
  /* animation: page-change 1s ease-in-out; */
}

.col-2 {
  display: flex;
  justify-content: space-evenly;
  justify-content: space-between;
	align-items: center;
  padding: 0 10%;
  
}


/* .local-link */
.main-page p a[href^='/#/']::before {
  content: "ðŸ“Œ";
  display: none;
  display: inline-block;
  width: 15px;
  height: 15px;
  margin-right: 5px;
}


.main-page p a[target="_blank"]::after {
  content: "(ðŸ”—â†—ï¸)";
  content: "â†—ï¸";
}

@keyframes page-change {
  0% {
    transform: translate(-100vw, -10%);
    transform: translateX(-100%);
  }
  100% {
    transform: translate(0vw, 0vh);
    transform: translateX(0%);
  }
}

@keyframes page-change-home {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0%);
  }
} 


@media only screen and (max-width: 425px) {
  .main-page h1 {
    font-size: 1.5rem;
  }

  .main-page p {
    font-size: .8rem;
  }
}

